import './Produit.scss';
import BoutonAjoutPanier from './BoutonAjoutPanier';

// Il est aussi possible de dstructurer un array directement dans les paramtres grce  :
export default function Produit({etatPanier : [panier, setPanier], nom, prix, pid}) {
    let quantite = panier[pid] ? panier[pid].quantite : 0;

    function ajouterAuPanier() {
        // S'il y en a dj, on l'augmente
        if (panier[pid]) {
            panier[pid].quantite++;
        }
        // Sinon, on le cre
        else {
            panier[pid] = {
                prix: prix,
                quantite: 1
            };
        }

        // Notifier React que le panier a chang
        /* 
            Il faut cloner l'objet panier pour que React dtecte que le panier a t chang,
            plutt que de le modifier 
        */
        setPanier(...panier);
    }

    return (
        <article className="Produit">
            <img src={"images-produits/" + pid + ".jpeg"} alt="{nom}"/>
            <div className="titre">{nom}</div>
            <div className="prix">{prix}</div>
            <BoutonAjoutPanier quantite={quantite} onClick={ajouterAuPanier}/>
        </article>
    );
}